import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import "./UpdateRole.scss";
import { IoMdAddCircle } from "react-icons/io";
import { FaCircleMinus } from "react-icons/fa6";
import axiosWithAuth from "../../../utils/axiosClienttok";

function UpdateRole() {
  const roles = useSelector((state) => state.RoleAndPermissionReducer.roles);
  const permissions = useSelector((state) => state.RoleAndPermissionReducer.permissions);

  const [selectedRolePermissions, setSelectedRolePermissions] = useState([]);
  const [remainingPermissions, setRemainingPermissions] = useState(permissions);
  const [selectedRole, setSelectedRole] = useState("");

  const handleSelect = (e) => {
    setSelectedRole([e.target.value]);
  };

  useEffect(() => {
    async function getRolePermission() {
      try {
        const response = await axiosWithAuth.get(`/group-detail/${selectedRole}/`);
        console.log(response?.data?.data?.permissions);
        const rolePermission = response?.data?.data?.permissions;
        console.log(rolePermission);
        if (rolePermission.length > 0) {
          const distinctPermissions = permissions.filter(
            (user) => !rolePermission.some((friend) => friend.id === user.id)
          );
          setRemainingPermissions(distinctPermissions);
          setSelectedRolePermissions(rolePermission);
        }
      } catch (error) {
        console.log(error);
      }
    }
    getRolePermission();
  }, [selectedRole]);

  const addPermission = (permission) => {
    setSelectedRolePermissions([...selectedRolePermissions, permission]);
    setRemainingPermissions(remainingPermissions.filter((p) => p !== permission));
  };

  const removePermission = (permission) => {
    setSelectedRolePermissions(selectedRolePermissions.filter((p) => p !== permission));
    setRemainingPermissions([...remainingPermissions, permission]);
  };

  async function handleUpdateRole(e) {
    e.preventDefault();
    const ids = selectedRolePermissions.map((item) => item.id);
    console.log(selectedRole);
    try {
      const response = await axiosWithAuth.patch(`/group-detail/${selectedRole}/`, { permissions: ids });
      console.log(response);
      if (response?.data?.status === true) {
        console.log(true);
      }
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <div className="create_sections">
      <div className="add_role">
        <select value={selectedRole} onChange={handleSelect}>
          <option value="">Select Role</option>
          {roles?.map((item) => (
            <option key={item?.id} value={item?.id}>
              {item?.name}
            </option>
          ))}
        </select>
      </div>

      <div className="add_permissions">
        <div className="list_of_permissions">
          <table border={1}>
            <tbody>
              <tr className="column_name">
                <th>All Permissions</th>
              </tr>
              {remainingPermissions?.map((permission, index) => (
                <tr className="column_value" key={index}>
                  <th>
                    {permission.name}{" "}
                    <button onClick={() => addPermission(permission)}>
                      <IoMdAddCircle />
                    </button>
                  </th>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <div className="added_list_of_permissions">
          <table border={1}>
            <tbody>
              <tr className="column_name">
                <th> Role Permissions</th>
              </tr>
              {selectedRolePermissions?.map((permission, index) => (
                <tr className="column_value" key={index}>
                  <th>
                    {permission?.name}{" "}
                    <button onClick={() => removePermission(permission)}>
                      <FaCircleMinus />
                    </button>
                  </th>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
      <div className="button_">
        <button onClick={handleUpdateRole}>Update role</button>
      </div>
    </div>
  );
}

export default UpdateRole;
