import React, { useEffect, useState } from "react";
import "./RoleManagement.scss";
import axiosWithAuth from "../../utils/axiosClienttok";
import { useDispatch, useSelector } from "react-redux";
import { getListofRoles, getListOfPermissions } from "../../Redux/Roleandpermissionreducer/RoleAndPermissionReducer";
import UpdateRole from "./updateRoles/UpdateRole";
import { toast } from "react-toastify";

function RoleManagement() {
  const roles = useSelector((state) => state.RoleAndPermissionReducer.roles);
  const permissions = useSelector((state) => state.RoleAndPermissionReducer.permissions);
  const dispatch = useDispatch();

  const [create, setCreate] = useState(false);
  const [update, setUpdate] = useState(false);
  const [createRole, setCreateRole] = useState("");
  const [selectedRole, setSelectedRole] = useState("");
  const [selectedPermissions, setSelectedPermissions] = useState([]);

  useEffect(() => {
    dispatch(getListofRoles());
    dispatch(getListOfPermissions());
  }, []);

  const handleRoleChange = (e) => {
    console.log(selectedRole);
    setSelectedRole(e.target.value);
  };

  const handleOpenCreate = () => {
    setCreate(true);
    setUpdate(false);
  };

  const handleOpenUpdate = () => {
    setUpdate(true);
    setCreate(false);
  };

  const handleAddPermission = (permission) => {
    if (!selectedPermissions.includes(permission)) {
      setSelectedPermissions([...selectedPermissions, permission]);
    } else {
      setSelectedPermissions(selectedPermissions.filter((item) => item !== permission));
    }
  };

  async function handleCreateRole(e) {
    e.preventDefault();
    const ids = selectedPermissions?.map((entry) => entry.id);
    try {
      const response = await axiosWithAuth.post("/create-group/", { name: createRole, permissions: ids });
      console.log(response);
      if (response?.data?.success === true) {
        toast.success("Role created");
      }
    } catch (error) {
      console.log(error);
      toast.error("Error");
    } finally {
      setCreate(false);
      setSelectedPermissions([]);
      dispatch(getListofRoles());
    }
  }

  return (
    <div className="role_mangement">
      <div className="inner_role_mangement">
        <div className="roles">
          <table border={1}>
            <tbody>
              <tr className="column_name">
                <th>Role</th>
              </tr>

              {roles &&
                roles?.map((item, index) => (
                  <tr className="column_value" key={index}>
                    <th>{item?.name}</th>
                  </tr>
                ))}
            </tbody>
          </table>
        </div>
        <div className="create_edit">
          <div className="button">
            <button className={create ? "colour" : "normal"} onClick={handleOpenCreate}>
              Create Role
            </button>
            <button className={update ? "colour" : "normal"} onClick={handleOpenUpdate}>
              Edit Role
            </button>
          </div>
          {create && (
            <div className="create_section">
              <div className="add_role">
                <input type="text" placeholder="Create Role" onChange={(e) => setCreateRole(e.target.value)} />
              </div>
              <div className="add_permissions">
                <div className="list_of_permissions">
                  <table border={1}>
                    <tbody>
                      <tr className="column_name">
                        <th>Permissions</th>
                      </tr>
                      {permissions &&
                        permissions?.map((item, index) => (
                          <tr
                            className={`column_value ${selectedPermissions.includes(item) ? "selected" : ""}`}
                            key={index}
                            onClick={() => handleAddPermission(item)}
                          >
                            <th>{item?.name}</th>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
                <div className="added_list_of_permissions">
                  <table border={1}>
                    <tbody>
                      <tr className="column_name">
                        <th> Added Permissions</th>
                      </tr>
                      {selectedPermissions &&
                        selectedPermissions.map((item, index) => (
                          <tr key={index} className="column_value">
                            <th onClick={handleRoleChange}>{item?.name}</th>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              </div>
              <div className="button_">
                <button onClick={handleCreateRole}>Add role</button>
              </div>
            </div>
          )}
          {update && <UpdateRole />}
        </div>
      </div>
    </div>
  );
}

export default RoleManagement;
